@media (max-width: 768px) {
    * {
        box-sizing: border-box;
    }
    .modal-title {
        max-width: 80%;
    }

    .modal {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        height: 100%;

        overflow: hidden;
        border-radius: 0;
    }
    .modal-dialog {
        position: fixed;
        margin: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        border-radius: 0;
    }
    .modal-content {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        text-align: center;
        left: 0;
        border-radius: 0;
        box-shadow: none;
        height: 100%;
        overflow: hidden;
        background-color: #13161A;

        border-radius: 0px;

    }


    ::-webkit-scrollbar {
        -webkit-appearance: none;
        width: 10px;
        background: #f1f3f5;
        border-left: 1px solid #d3d9e0;
    }
    ::-webkit-scrollbar-thumb {
        background: #b5c0cb;
    }


}

